import { Component, OnInit } from '@angular/core';
import { UpdateSecureWordService } from './updateSecureWordService';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AlertController, PopoverController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { PopoverContentPage } from '../popover-content/popover-content.page';
import { AuthHandlerProvider } from 'src/providers/auth-handler/auth-handler';
import { JsonStoreProvider } from 'src/providers/json-store/json-store';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { LoginService } from '../login/loginService';
import * as HttpStatus from 'http-status-codes';
import { Router } from '@angular/router';
import { TranslateConfigService } from 'src/app/services/translate-config.service';


@Component({
  selector: 'app-update-secure-word',
  templateUrl: './update-secure-word.page.html',
  styleUrls: ['./update-secure-word.page.scss'],
})
export class UpdateSecureWordPage implements OnInit {
  selectedLanguage:string;
  changeSecureWordForm: FormGroup;
  isShowingLoader: boolean = false;
  errorMsg: any;
  isSuccess: boolean;
  str1 : any ="five";
  gotoDashbaordAfterUpdate: boolean = false;
  secureWordImage:any="assets/imgs/placeholder-image.png";

  constructor(
    public utils: UtilService,private config: ConfigProvider,
    private changeSecureWordService: UpdateSecureWordService,
    private alertCtrl: AlertController, public authHandler: AuthHandlerProvider,
    public jsonStore: JsonStoreProvider,private logger: LoggerServiceProvider,
    public loginService:LoginService, public popOverCtrl:PopoverController,
    public translate: TranslateService,private router: Router,
    private translateConfigService: TranslateConfigService,
  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
    this.translateConfigService.setLanguage('en');

    this.utils.analytics("changeSecureWord");
    this.config.currentPage.pageName = "UpdateSecureWordPage";
    //this.gotoDashbaordAfterUpdate=history.state.gotoDashboard;
    this.secureWordImage=this.config.secureWordImage;
    this.utils.dissmisProgressLoader();
   }

  ngOnInit() {
    this.changeSecureWordForm = new FormGroup({
      newSecureWord: new FormControl('', [Validators.required,  Validators.minLength(4),Validators.maxLength(10)]),
      });
  }
  changeSecureWord() {
    if (this.changeSecureWordForm.valid) {
      let newSecureWordVal = this.changeSecureWordForm.controls['newSecureWord'].value.trim();
        if (this.utils.isDeviceOnLine()) {
          this.utils.showProgressLoader();
          this.changeSecureWordService.changeSecureWordService(newSecureWordVal).then((res) => {
            try {
              if(res['responseJSON']){
                if (res['responseJSON']['statusCode'] == HttpStatus.ACCEPTED || res['responseJSON']['statusCode'] == HttpStatus.OK) {
                  let msg = this.translate.instant('update_secure_word.change_wrd_success_txt');
                  let errorMsg = res['responseJSON']['userMessage'] || msg;
                  this.isSuccess = true;
                  this.utils.dissmisProgressLoader();
                  this.showDialog("", errorMsg);
                }
              } else {
                let errorMsg = this.translate.instant('update_secure_word.change_pwd_success_txt');
                this.isSuccess = true;
                this.utils.dissmisProgressLoader();
                this.showDialog("", errorMsg);
              }
            } catch (e) {
              this.utils.dissmisProgressLoader();
              this.isSuccess = false;
              this.logger.error("loginSuccess catch eror : " + JSON.stringify(e));
              let errorMsg = this.translate.instant('generic_msgs.server_err');
              this.utils.showAlert("", errorMsg);
            }
          }, (err) => {
            this.utils.dissmisProgressLoader();
            this.isSuccess = false;
            let errorMsg = this.utils.handleServiceException(err);
            this.utils.showAlert("", errorMsg);
          });
        } else {
          this.utils.dissmisProgressLoader();
          this.isSuccess = false;
          let errorMsg = this.translate.instant('generic_msgs.network_err');
          this.utils.showAlert("", errorMsg);
        }

    }
  }
  showDialog(title: string, message: string) {
    let buttons = [{
         text: 'Ok',
         handler: () => {
         	if(this.gotoDashbaordAfterUpdate){
            this.router.navigate(['/dashboard-after-login'], { state: {animate: true, direction: 'forward'} });
         	//this.navCtrl.setRoot(DashboardAfterLoginPage, { animate: true, direction: 'forward' });
         	}
         	else{
         	//this.navCtrl.pop();
         	}

         }
     }];
     const alert: any = this.alertCtrl.create({
      header: title,
      message: message,
      buttons: buttons
    }).then((alert) => alert.present());
    }
    async openPopup(e: Event) {
      const popover = await this.popOverCtrl.create({
        component: PopoverContentPage,
        showBackdrop: false,
        event: e,
        componentProps: { msg: this.str1 },
        cssClass: 'custom-popover'
      });
      await popover.present();
    }
}
